<template>
  <nav class="lnb">
    <div class="lnb-menu">
      <div class="lnb-menu-group">
        <h2 class="label flexmb-[4px] typo-caption-lg">üé® Foundation</h2>
        <ul class="lnb-list">
          <li class="lnb-list-item" v-for="(item, index) in menuItems" :key="index">
            <a
              href="javascript:;"
              class="typo-body-lg fw-medium"
              :class="{ 'on': index === selectedMenuItem }"
              @click="selectMenuItem(index, item.path)"
            >
              {{ item.text }}
            </a>
          </li>
        </ul>
      </div>
      <!--//lnb-menu-group-->
      <div class="lnb-menu-group">
        <h2 class="label flexmb-[4px] typo-caption-lg">üé® Component</h2>
        <ul class="lnb-list">
          <li class="lnb-list-item" v-for="(item, index) in componentItems" :key="index">
            <a
              href="javascript:;"
              class="typo-body-lg fw-medium"
              :class="{ 'on': item === selectedComponentItem }"
              @click="selectComponentItem(item, item.path)"
            >
              {{ item.text }}
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "LnbMenu",
  data() {
    return {
      menuItems: [
        { text: "Color", path: "/Color" },
        { text: "Typography", path: "/typo" },
        { text: "Spacing", path: "/spacing" },
        { text: "Box Shadow", path: "/box-shadow" },
      ],
      componentItems: [
        { text: "Button", path: "/button" },
        { text: "Forms", path: "/forms" },
      ],
      selectedMenuItem: 0, // Ï¥àÍ∏∞ ÏÑ†ÌÉù Ìï≠Î™©Ïùò Ïù∏Îç±Ïä§ ÏÑ§Ï†ï
      selectedComponentItem: '', // ÏÑ†ÌÉùÎêú Component Ìï≠Î™© Ï¥àÍ∏∞Ìôî
    };
  },
  created() {
    // ÌéòÏù¥ÏßÄ Î°úÎî© Ïãú ÌòÑÏû¨ Í≤ΩÎ°úÎ•º ÌôïÏù∏ÌïòÍ≥† ÏÑ†ÌÉùÎêú Foundation Ìï≠Î™© ÏÑ§Ï†ï
    const currentPath = this.$route.path;
    const foundationItem = this.menuItems.find(item => item.path === currentPath);
    if (foundationItem) {
      this.selectedMenuItem = this.menuItems.indexOf(foundationItem);
    }
  },
  methods: {
    selectMenuItem(index, path) {
      this.selectedMenuItem = index; // ÏÑ†ÌÉùÌïú Foundation Ìï≠Î™© ÏóÖÎç∞Ïù¥Ìä∏
      // ÎùºÏö∞ÌÑ∞Î•º ÌÜµÌïú ÌéòÏù¥ÏßÄ Ïù¥Îèô
      this.$router.push(path);
    },
    selectComponentItem(item, path) {
      this.selectedComponentItem = item; // ÏÑ†ÌÉùÌïú Component Ìï≠Î™© ÏóÖÎç∞Ïù¥Ìä∏
      // ÎùºÏö∞ÌÑ∞Î•º ÌÜµÌïú ÌéòÏù¥ÏßÄ Ïù¥Îèô
      this.$router.push(path);
    },
  },
};
</script>

